'use client';
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import Image from 'next/image';
import { Shield, Users, Activity, Lock, Unlock } from 'lucide-react';
import Sidebar from '@/components/dashboard/Sidebar';
import Header from '@/components/dashboard/Header';
import StatsCard from '@/components/dashboard/StatsCard';

export default function DashboardPage() {
  const [user, setUser] = useState<any>(null);
  const [qrCode, setQrCode] = useState('');
  const [secret, setSecret] = useState('');
  const [otp, setOtp] = useState('');
  const [msg, setMsg] = useState('');
  const router = useRouter();

  useEffect(() => {
    const stored = localStorage.getItem('user');
    if (!stored) {
      router.push('/login');
    } else {
      setUser(JSON.parse(stored));
    }
  }, [router]);

  const handleLogout = () => {
    localStorage.removeItem('user');
    router.push('/login');
  };

  const enable2FA = async () => {
    if (!user?.id) return;
    try {
      const res = await fetch('/api/auth/2fa/generate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ userId: user.id }),
      });
      const data = await res.json();
      if (data.qrCode) {
        setQrCode(data.qrCode);
        setSecret(data.secret);
        setMsg('Scan to activate');
      }
    } catch(e) { console.error(e); }
  };

  const verify2FA = async () => {
    if (!user?.id) return;
    try {
      const res = await fetch('/api/auth/2fa/verify', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ userId: user.id, token: otp }),
      });
      const data = await res.json();
      if (data.success) {
        setMsg('2FA Enabled Successfully!');
        setQrCode('');
        setSecret('');
        // Update local user state to show verified
        setUser({ ...user, totp_enabled: true }); 
      } else {
        setMsg('Invalid Code. Try again.');
      }
    } catch(e) { console.error(e); }
  };

  if (!user) return <div className="flex min-h-screen items-center justify-center bg-slate-900 text-white">Loading Security Environment...</div>;

  return (
    <div className="flex min-h-screen bg-slate-50 dark:bg-slate-900">
      <Sidebar onLogout={handleLogout} />
      
      <div className="flex-1 flex flex-col lg:pl-64 transition-all duration-300">
        <Header username={user.username} />
        
        <main className="flex-1 p-6 space-y-6 overflow-y-auto">
          {/* Stats Section */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            <StatsCard 
              title="Total Users" 
              value="12,345" 
              change="+12% this month" 
              icon={Users} 
              color="bg-blue-500" 
            />
            <StatsCard 
              title="Active Sessions" 
              value="423" 
              icon={Activity} 
              color="bg-green-500" 
            />
            <StatsCard 
              title="2FA Enforced" 
              value="89%" 
              change="+5% from last week" 
              icon={Shield} 
              color="bg-purple-500" 
            />
            <StatsCard 
              title="Failed Attempts" 
              value="23" 
              icon={Lock} 
              color="bg-red-500" 
            />
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* 2FA Activation Panel */}
            <div className="lg:col-span-2 rounded-xl border border-gray-100 bg-white p-6 shadow-sm dark:bg-slate-800 dark:border-slate-700">
              <div className="mb-6 flex items-center justify-between">
                <h3 className="text-lg font-bold text-slate-800 dark:text-white">Security Configuration</h3>
                <span className={`px-3 py-1 rounded-full text-xs font-semibold ${user.totp_enabled || msg.includes('Success') ? 'bg-green-100 text-green-700' : 'bg-yellow-100 text-yellow-700'}`}>
                  {user.totp_enabled || msg.includes('Success') ? 'SECURE' : 'ATTENTION NEEDED'}
                </span>
              </div>

              <div className="space-y-6">
                <div className="flex items-start gap-4 p-4 bg-slate-50 rounded-lg border border-slate-100 dark:bg-slate-900 dark:border-slate-700">
                  <div className="p-2 bg-blue-100 text-blue-600 rounded-lg dark:bg-blue-900 dark:text-blue-300">
                    <Shield className="w-6 h-6" />
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 dark:text-white">Two-Factor Authentication (TOTP)</h4>
                    <p className="text-sm text-slate-500 mt-1 dark:text-slate-400">
                      Research Note: Enabling this generates a secret key stored in the <code>users</code> table.
                    </p>
                  </div>
                </div>

                {!qrCode && !msg.includes('Successfully') && !user.totp_enabled ? (
                  <button 
                    onClick={enable2FA}
                    className="flex items-center justify-center w-full sm:w-auto px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-medium rounded-lg transition-colors shadow-sm"
                  >
                    <Unlock className="w-4 h-4 mr-2" />
                    Activate 2FA
                  </button>
                ) : null}

                {qrCode && (
                  <div className="animate-in fade-in slide-in-from-top-4 duration-500">
                    <div className="p-6 bg-white border-2 border-dashed border-blue-200 rounded-xl dark:bg-slate-800 dark:border-slate-600">
                      <p className="text-center font-medium text-slate-700 mb-6 dark:text-slate-300">{msg || "Scan with Authenticator App"}</p>
                      
                      <div className="flex flex-col md:flex-row items-center justify-center gap-8">
                        <div className="p-2 bg-white rounded-lg shadow-sm border border-slate-200">
                          <Image src={qrCode} alt="QR Code" width={180} height={180} className="mix-blend-multiply" />
                        </div>

                        <div className="flex-1 space-y-4 max-w-sm w-full">
                          <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-3">
                            <label className="block text-xs font-bold text-yellow-800 uppercase tracking-wider mb-1">Forensic Artifact: Secret Key</label>
                            <code className="block text-sm font-mono text-yellow-900 bg-yellow-100/50 p-2 rounded break-all select-all cursor-text">
                              {secret}
                            </code>
                          </div>

                          <div>
                            <label className="block text-sm font-medium text-slate-700 mb-2 dark:text-slate-300">Verification Code</label>
                            <div className="flex gap-2">
                              <input 
                                type="text" 
                                value={otp}
                                onChange={(e) => setOtp(e.target.value)}
                                className="flex-1 p-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none text-center text-lg tracking-[0.5em] font-mono dark:bg-slate-900 dark:border-slate-600 dark:text-white"
                                placeholder="000000"
                                maxLength={6}
                              />
                              <button 
                                onClick={verify2FA}
                                className="px-6 py-2.5 bg-green-600 hover:bg-green-700 text-white font-medium rounded-lg transition-colors shadow-sm"
                              >
                                Verify
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
                
                {(msg.includes('Successfully') || user.totp_enabled) && (
                   <div className="p-4 bg-green-50 text-green-700 border border-green-200 rounded-lg flex items-center gap-3">
                     <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                       <Shield className="w-4 h-4 text-green-600" />
                     </div>
                     <div>
                       <p className="font-semibold">Account Secured</p>
                       <p className="text-sm opacity-90">TOTP authentication is active on this account.</p>
                     </div>
                   </div>
                )}
              </div>
            </div>

            {/* Users List Panel (Static for Design) */}
            <div className="rounded-xl border border-gray-100 bg-white p-6 shadow-sm dark:bg-slate-800 dark:border-slate-700">
              <h3 className="text-lg font-bold text-slate-800 mb-4 dark:text-white">Recent Users</h3>
              <div className="space-y-4">
                {[1, 2, 3, 4, 5].map((i) => (
                  <div key={i} className="flex items-center justify-between p-3 hover:bg-slate-50 rounded-lg transition-colors dark:hover:bg-slate-700/50">
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 rounded-full bg-slate-200 flex items-center justify-center text-slate-600 font-bold text-xs">
                        U{i}
                      </div>
                      <div>
                        <p className="text-sm font-medium text-slate-900 dark:text-white">User {i}</p>
                        <p className="text-xs text-slate-500 dark:text-slate-400">user{i}@example.com</p>
                      </div>
                    </div>
                    <span className={`text-xs px-2 py-1 rounded-full ${i % 2 === 0 ? 'bg-green-100 text-green-600' : 'bg-gray-100 text-gray-500'}`}>
                      {i % 2 === 0 ? ' Active' : 'Offline'}
                    </span>
                  </div>
                ))}
              </div>
              <button className="w-full mt-4 text-sm text-blue-600 hover:text-blue-700 font-medium py-2">
                View All Users
              </button>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}
